.page {
  font-family: Cinzel;
  scroll-behavior: smooth;
  &__body {
    margin: 0;
  }
  &__menu {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    pointer-events: none;
    opacity: 0;

    &:target {
      pointer-events: all;
      opacity: 1;
    }
  }
}
.page:has(.page__menu:target) {
  overflow: hidden; /* not to scroll the page */
}
